.form-checkbox {
  padding: 0;
  margin: 0 0 32px 0;
  background-color: $gray-300;
  border: 2px solid $gray-200;
  border-radius: 3px;

  @media (max-width: $mobile-width) {
    border: none;
  }

  @media (min-width: $tablet-width) {
    background-color: $white;
    margin-left: auto;
    margin-right: auto;
  }
}

.form-checkbox__wrapper {
  width: 320px;
  margin: 0 auto;
  padding-bottom: 30px;

  @media (min-width: $tablet-width) {
    width: auto;
    margin: auto;
    padding-bottom: 37px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 0;
  }
}

.form-checkbox label {
  font-size: 18px;
  margin-left: 24px;
  position: relative;
  cursor: pointer;
}

.form-checkbox__list {
  list-style: none;
  margin-top: 43px;
  margin-left: 55px;
  padding: 0;

  @media (min-width: $tablet-width) {
    margin-top: 48px;
    margin-left: 93px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 73px;
    margin-bottom: 8px;
  }
}

.form-checkbox__item {
  margin-bottom: 50px;

  @media (min-width: $desktop-width) {
    justify-self: flex-start;

    &:nth-of-type(1) {
      order: 1;
    }

    &:nth-of-type(2) {
      order: 4;
    }

    &:nth-of-type(3) {
      order: 2;
      margin-left: 10px;
    }

    &:nth-of-type(4) {
      order: 5;
      margin-left: 10px;
    }

    &:nth-of-type(5) {
      order: 3;
    }

    &:nth-of-type(6) {
      order: 6;
    }
  }
}

.form-checkbox__item:last-child {
  margin-bottom: 0;
}

.form-checkbox__input + label::before,
.form-checkbox__input + label::after {
  content: "";
  position: absolute;
}

.form-checkbox__input + label::before {
  left: -59px;
  top: -7px;
  width: 39px;
  height: 39px;
  border: 2px solid $gray-200;
  border-radius: 1px;
  background-color: $white;
}

.form-checkbox__input:checked + label::after {
  left: -50px;
  top: 4px;
  width: 22px;
  height: 16px;
  background-image: url("../img/checked.svg");
  background-repeat: no-repeat;
}

.form-checkbox__input:focus + label::before,
.form-checkbox__input:hover + label::before {
  border: 2px solid $gray-500;
}

.form-checkbox__input:active + label::before {
  border: 2px solid $pink;
}
